@mixin respond($breakpoint) { 
    @if $breakpoint == phone {
       @media only screen and (max-width: 37.5em) { @content };   //600px
    }
    @if $breakpoint == tab-port {
       @media only screen and (max-width: 56.25em) { @content };    //900px
    }
    @if $breakpoint == tab-land {
       @media only screen and (max-width: 75em) { @content };    //1200px
    }
    @if $breakpoint == big-desktop {
       @media only screen and (min-width: 112.5em) { @content };    //1800px
    }
}

.work-item {

    &__content {
        position: relative;
        background-color: white;
        padding: 0;
        margin: 6.5px;
        border: 1px solid #dfe4e8;
        border-radius: 5px;
        display: flex;
        justify-content: center;
    
        .image {
            max-width: 375px;
            max-height: 380px;

            @include respond(phone) {
                max-width: 300px;
                max-height: 304px;
            }
        }

        .tags-box {
            position: absolute;
            top: 10px;
            left: 10px;
            display: none;
            color: #fff;
            font-size: 20px;
            overflow: hidden;
            width: 80%;
        }
        
        a {
            text-decoration: none;
        }

        .btn-box {
            width: 100%;
            position: absolute;
            top: 30%;
        }
        
        .button_1,
        .button_2 {
            box-sizing: border-box;
            margin-bottom: 2px;
            text-align: center;
            border: none;
            border-radius: 4px;
            background-color: #5c6ac4;
            color: #fff;
            cursor: pointer;
            font-size: 1.14286rem;
            font-weight: 600;
            text-transform: uppercase;
            box-shadow: 0 10px 20px 0 rgba(0,0,0,.25);
            transition: box-shadow .25s linear;
            width: 60%;
            margin: 15px auto;
            display: none;
            line-height: 2.5;

            @include respond(tab-port){
                width: 70%;
            }

            @include respond(phone){
                width: 80%;
            }

            .icon {
                height: auto;
                padding-right: 10px;
            }
        }

        .button_1 {
            background-color: #5c6ac4;
        }

        .button_2 {
            background-color: #fff;
            color: #202e78;
        }

        &:hover {
            .image {
                -webkit-filter:brightness(.5);
            }

            .tags-box {
                display: inline-block;
            }

            .button_1,
            .button_2 {
                display: block;
                
                &:hover {
                    background-color: #202e78;
                    color: white;
                }
            }
            
        }
    }

    &__footer {
        padding: 2px 10px;
        margin-bottom: 20px;
        max-width: 300px;
        text-align: center;

        &--text {
            font-size: 20px;
            font-weight: 500;
            margin-top: 0;
        }
    }
}