.content {
    width: 70%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px auto;
    font-family: 'Montserrat', sans-serif;

    @include respond(tab-port) {
        width: 90%;
    }

    .content__header {
        text-align: center;
        padding-bottom: 20px;
        width: 100%;

        .title {
            font-size: 38px;

            &:after {
                content: ""; /* This is necessary for the pseudo element to work. */ 
                display: block; /* This will put the pseudo element on its own line. */
                margin: 0 auto; /* This will center the border. */
                width: 100px; /* Change this to whatever width you want. */
                padding-top: 6px; /* This creates some space between the element and the border. */
                border-bottom: 1px solid orange; /* This creates the border. Replace black with whatever color you want. */
            }

            @include respond(phone) {
                font-size: 33px;
            }
        }

        .profile-photo {
            width: 150px;
            height: auto;
            transform: rotate(15deg);
        }
    }

    .text-box {
        width: 100%; 

        .bio {
            font-size: 24px;
            font-weight: 600;
            text-align: center;
        }

        ul {
            font-size: 24px;
            line-height: 1.3;
            background-color: #f7f8fa; 
            padding: 20px;
            padding-left: 40px;

            .list_title {
                font-weight: 600;
                line-height: 2;
                font-size: 24px;
            } 
            
            strong {
                font-weight: 500;
            }
        }
    }
}